/* Diagram styles */

.quiz-container {
    display: block;
    width: 80%;
    align-items: center;
    justify-content: center;
    background-color: var(--color-dark-white);
    margin: 10px auto 10px;
    padding: 10px;
    box-shadow: var(--shadow-style);
    border-radius: 5px;
}

.link-back {
  background-color: var(--color-pink);
  color: var(--color-dark-white);
  font-size: 20px;
  padding: 10px;
  position: relative;
  top: 20px;
  left: 10px;
}

.quiz-inner-container {
    background-color: var(--color-white);
    margin: 20px;
    padding: 10px;
    box-shadow: var(--shadow-style);
}

.quiz-questions {
    width: 100%;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 2px 1px 2px 1px;
    border-radius: 5px;
    color: var(--color-white);
    background-color: var(--color-darkblue);
    font-weight: lighter;
}

.quiz-h1 {
  text-align: center;
  color: var(--color-darkblue);
}

.quiz-variants-label {
  font-size: 20px;
  color: var(--color-darkblue);
}

.submit-button {
    display: block;
    width: 100%;
    padding: 0.75rem;
    background: var(--color-pink);
    color: var(--color-dark-white);
    border-radius: 2px;
    cursor: pointer;
    border: none;
    font-size: 20px;
    margin-bottom: 20px;
}

.quiz-variants-span {
    padding: 5px;
    margin: 5px;
    border-radius: 5px;
    font-weight: 900;
}

.true {
  color: green;;
}

.false {
  color: red;
}

.quiz-variants-div {
    margin: 10px 0 10px;
}


@media (min-width: 576px) {
    .quiz-container {
      max-width: 550px;
    }
  }
  
  @media (min-width: 768px) {
    .quiz-container {
      max-width: 750px;
    }
  }